#HEADER_1 {
    bottom: 0px;
    box-sizing: border-box;
    color: rgb(97, 97, 97);
    height: 200px;
    left: 0px;
    max-width: 1024px;
    position: relative;
    right: 0px;
    text-decoration: none solid rgb(97, 97, 97);
    text-size-adjust: 100%;
    top: 0px;
    width: 1024px;
    column-rule-color: rgb(97, 97, 97);
    perspective-origin: 512px 82px;
    transform-origin: 512px 82px;
    caret-color: rgb(97, 97, 97);
    background: rgba(255, 255, 255, 0.7) none repeat scroll 0% 0% / auto
        padding-box border-box;
    border: 0px none rgb(97, 97, 97);
    font: normal normal 400 normal 13px / normal open_sansregular;
    margin: 0px 162.5px;
    outline: rgb(97, 97, 97) none 0px;
} /*#HEADER_1*/

#HEADER_1:after {
    color: rgb(97, 97, 97);
    text-decoration: none solid rgb(97, 97, 97);
    text-size-adjust: 100%;
    column-rule-color: rgb(97, 97, 97);
    caret-color: rgb(97, 97, 97);
    border: 0px none rgb(97, 97, 97);
    font: normal normal 400 normal 13px / normal open_sansregular;
    outline: rgb(97, 97, 97) none 0px;
} /*#HEADER_1:after*/

#HEADER_1:before {
    color: rgb(97, 97, 97);
    text-decoration: none solid rgb(97, 97, 97);
    text-size-adjust: 100%;
    column-rule-color: rgb(97, 97, 97);
    caret-color: rgb(97, 97, 97);
    border: 0px none rgb(97, 97, 97);
    font: normal normal 400 normal 13px / normal open_sansregular;
    outline: rgb(97, 97, 97) none 0px;
} /*#HEADER_1:before*/

